:root[class="light"] {
  --bg-page: #f7f5fa;  //   
  --bg-complementary: linear-gradient(90deg, #453b68 42%, rgba(62,84,172,1) 100%); //    ( // )
  --bg-btn-color: #3E54AC; //   ()
  --accent-color: #3E54AC; //    -         
  --font-main-color: black; //   
  --font-secondary-color: white; //    (     bg-compelemntary)
  --auxiliary-color: #797979; //         
  --inactive-bg-color: #aaaaaa; //      /  / 
  --color-link-inactive: #ccccc5; //     /
  --error-color: #b90101; 
  --success-color: #006600;
  --bg-neutral: rgba(255, 255, 255, 0.4); //    ( /  )
  --bg-popup-color: #ffffff; //       
  --popup-contrast-color: black; //    ,    --bg-popup-color
  --popup-shadow-color: rgb(0 0 0 / 10%); //    
  --input-bg-color: #e8f0fe; //    
  --bg-initial-btn-color: #3E54AC;
  --bg-auxiliary-color: #f2f2f2;
  --border-color: #aaaaaa; //   (//  ....)
  --font-intro-color: #ffffff;
  --bg-intro: linear-gradient(90deg, #443C68, #1e1c22);
 }

:root[class="dark"] {
  --bg-page: radial-gradient(circle, rgba(38,34,40,1) 0%, rgba(50,51,55,1) 100%);
  --bg-complementary: linear-gradient(90deg, #b0b8d4 40%, rgba(218,172,104,1) 70%);
  --bg-btn-color: #b0b8d4;
  --accent-color: #ebac54;
  --font-main-color: #edecfd;
  --font-secondary-color: black;
  --auxiliary-color: #b3b3b3;
  --inactive-bg-color: #aaaaaa;
  --color-link-inactive: #3e3e41;
  --error-color: #b90101;
  --success-color: #006600;
  --bg-neutral: rgba(39, 39, 42, 0.4);
  --bg-popup-color: #1F2027;
  --popup-contrast-color: #edecfd;
  --popup-shadow-color: rgba(0, 0, 0, 0.6);
  --input-bg-color: #dfdfdf;
  --bg-initial-btn-color: #ebac54;
  --bg-auxiliary-color: #f2f2f2;
  --border-color: #313131;
  --font-intro-color: #ffffff;
  --bg-intro: linear-gradient(90deg, #2c2c2c, #1F2027);
}
