.wrapper {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  height: 100%;
  .contents {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 1rem;
    .expressions {
      display: flex;
      justify-content: space-around;

      @media screen and (max-width: 650px) {
        width: 100%;
        flex-direction: column;
        align-items: center;
        gap: 5rem
      }
    }
    .check-icon {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 5rem;

      @media screen and (max-width:650px) {
        top: 30%;
      }
    }
  }
}
